
div {
  border: 1px solid #bcbcbc;
}
.topcorner{ 
position:absolute; 
top:0; 
right:0;
}
.space{
  border: none;
  height:30px;
}
.jb-table {
  display: table;
  width: 100%;
}
.jb-table-row {
  display: table-row;
}
.jb-table-cell {
  display: table-cell;
  padding: 0px 20px;
  height: 50px;
}
.jb-top {
  vertical-align: top;
}
.jb-middle {
  vertical-align: middle;
}
.jb-bottom {
  vertical-align: bottom;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
